import React, { useState } from 'react'
import { useAuth } from '../Context/AuthContext'
import { Button } from 'react-bootstrap'

const PedidosDetailsStateInfo = () => {
    const { userType } = useAuth()

    const [show, setShow] = useState(false)

    const showAction = () => {
        setShow(show => !show)
    }

    return (
        <div className='stateChange'>
            <Button onClick={() => showAction()} variant='secondary' className='mb-3'>
                Ms informacin sobre los estados
            </Button>
            <div style={{ display: show ? 'block' : 'none' }} className='stateChangeInfo'>
                <p className='mb-0'>
                    <small>
                        {userType === 'reparticion'
                            ? 'El equipo de comunicacin podr cambiar el estado de "Creado" a "En curso" y una vez finalizado lo cambiar a "Finalizado".'
                            : 'Como parte del equipo de comunicacin podrs cambiar el estado de "Creado" a "En curso" si ya lo vas a empezar y una vez que lo finalices a "Finalizado".'}
                    </small>
                </p>
                <p className='mb-0'>
                    <small>
                        {userType === 'reparticion'
                            ? 'Si considers que el trabajo est listo podrs cambiarle el estado a "Cerrado", esto har que se deje de visualizar en esta pgina.'
                            : 'Si la reparticin que realiz el pedido esta conforme con el mismo cambiar el estado a "Cerrado"'}
                    </small>
                </p>
                <p className='mb-0'>
                    <small>De esta forma evitamos que se acumulen muchos pedidos.</small>
                </p>
                <p>
                    <small>
                        *El pedido cambiar su estado a "Cerrado" autmaticamente luego de 7 das de haber sido "Finalizado".
                    </small>
                </p>
            </div>
        </div>
    )
}

export default PedidosDetailsStateInfo
